import * as React from 'react';
import classNames from 'classnames';
import { Container, Row, Column } from '../components/grid/Grid';
import BlockContent from '../components/blockContent/BlockContent';
import Layout from '../components/layout/Layout';
import PageTitle from '../components/pageTitle/PageTitle';
import Section from '../components/section/Section';
import LocationsSection from '../../domain/location/containers/LocationsSection';
import blockContentSerializerBrown from '../components/blockContent/blockContentSerializerBrown';
import Slider from '../components/slideshow/Slider';
import styles from './PageTreatment.module.css';

type Props = {
  pageContext: {
    data: any;
  };
} & React.HTMLAttributes<HTMLElement>;

const PageTreatment: React.FunctionComponent<Props> = ({ className, ...props }) => {
  const { data } = props.pageContext;
  const gatsbyImages = data.image.map(image => image.asset);
  return (
    <Layout className={styles.layout}>
      <Section>
        <PageTitle theme="brown" title="" className={styles.pageTitle} />
        <Container>
          <Row>
            <Column xs={12} md={5} className={styles.slider}>
              <Slider images={gatsbyImages} />
            </Column>
            <Column xs={12} mdOffset={1} md={6} className={styles.container}>
              <div className={styles.titlePriceContainer}>
                <h1 className={styles.treatmentTitle}>{data.title}</h1>
                <h1 className={classNames(styles.treatmentTitle, styles.priceTag)}>
                  {data.price},-
                </h1>
              </div>
              <BlockContent
                blocks={data.detailedDescription}
                serializers={blockContentSerializerBrown}
              />
            </Column>
          </Row>
        </Container>
      </Section>
      <LocationsSection />
    </Layout>
  );
};

export default PageTreatment;
